import React from 'react';

const Header = () => {
  return (
    <header className="app-header">
      <h1 className="app-logo">JSON Counter 4000</h1>
      <p className="app-description">
        Best way to count objects in your JSON
      </p>
    </header>
  );
};

export default Header;


